.page-container {
  max-width: 112rem;
  margin: 0 auto;
  padding: 0 1.25rem;
  box-sizing: border-box;
}
@media (min-width: 37.5em) {
  .page-container {
    padding: 0 2rem;
  }
}
@media (min-width: 56.25em) {
  .page-container {
    padding: 0 3rem;
  }
}
@media (min-width: 75em) {
  .page-container {
    padding: 0 2.5rem 0 1.25rem;
  }
}

.page-container__breakout--centered {
  margin-left: -1.25rem;
  margin-right: -1.25rem;
}
.page-container__breakout--centered-revert {
  padding-left: 1.25rem;
  padding-right: 1.25rem;
}
@media (min-width: 37.5em) {
  .page-container__breakout--centered {
    margin-left: -2rem;
    margin-right: -2rem;
  }
  .page-container__breakout--centered-revert {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}
@media (min-width: 56.25em) {
  .page-container__breakout--centered {
    margin-left: -3rem;
    margin-right: -3rem;
  }
  .page-container__breakout--centered-revert {
    padding-left: 3rem;
    padding-right: 3rem;
  }
}
@media (min-width: 75em) {
  .page-container__breakout--centered {
    margin-left: 0;
    margin-right: 0;
  }
  .page-container__breakout--centered-revert {
    padding-left: 0;
    padding-right: 0;
  }
}

@media (min-width: 75em) {
  .page-container__inner {
    /* autoprefixer grid: on */
    display: -ms-grid;
    display: grid;
    grid-column-gap: 1.25rem;
    -ms-grid-columns: (minmax(0, 1fr))[13];
    grid-template-columns: repeat(13, minmax(0, 1fr));
    margin: 0 -0.625rem;
  }
  @supports (display: grid) {
    .page-container__inner {
      margin: 0;
    }
  }
  @supports not (display: grid) {
    .page-container__inner {
      display: flex;
    }
  }

  .page-container__aside {
    /* autoprefixer grid: on */
    -ms-grid-column: 1;
    grid-column: 1;
    -ms-grid-row: 1;
    grid-row: 1;
    -ms-grid-row-align: end;
        align-self: end;
    align-self: flex-end;
    margin: 0 0.625rem;
  }
  @supports (display: grid) {
    .page-container__aside {
      margin: 0;
    }
  }
  @supports not (display: grid) {
    .page-container__aside {
      flex: none;
      width: calc(100% / 13 - 1.25rem);
      order: -1;
    }
  }

  .page-container__main {
    /* autoprefixer grid: on */
    -ms-grid-column: 2;
    -ms-grid-column-span: 12;
    grid-column: 2/14;
    -ms-grid-row: 1;
    grid-row: 1;
    margin: 0 0.625rem;
  }
  @supports (display: grid) {
    .page-container__main {
      margin: 0;
    }
  }
  @supports not (display: grid) {
    .page-container__main {
      flex: none;
      width: calc((100% / 13) * 12 - 1.25rem);
    }
  }
}
